 border-radius:0 0 8px 0;
}
.robot-comment button:last-child{
   border-left:  1px solid #7bb3db;
}
.robot-comment .isClicked,.service-comment .isClicked{
   background: #93c3e5;
}
button:active, button[disabled]{
   color: #999;
}


.windowContent .button-p{
   margin-top: 15px;
   border-top: 1px solid #77b0d9;
}
.J-confirm{
   width: 117px;
   height: 30px;
   border: none;
   background: none;
   border-left: 1px solid #77b0d9;
}
.J-cancel{
   width: 124px;
   height: 30px;
   background: none;
   border: none;
}
.button-p button[disabled]{
   cursor: default;
   background-color: none;
   background-image: none;
   border-color: none;
   box-shadow: none;
}
.button-p button:active,.button-p button[disabled]{
  background-color: none;
}
.btnClicked{
   background: #93c3e5;
}
.user .avatar-img,.avatar-service{
 width: 34px;
 height: 34px;
 border-radius: 2px;
}
section article header{
 margin-top:10px;

}
section article li{
 width: 202px;
 padding:10px 0;
 list-style:none;
 border-top: solid 1px #afafaf;
 overflow: hidden;
 text-overflow:ellipsis;
 white-space: nowrap;
 color:#2982e5;
}
section article li:last-child{
 padding-bottom:0;
}
section,section a{
 font-size:16px;
}
.arrow-down,.arrow-up{
 width:10px;
 height:6px;
 background:url("https://i.alipayobjects.com/i/ecmng/png/201404/2O4b2MEDzR.png") no-repeat;
 background-size:100%;
 padding:5px 0;
 margin:5px auto 0;
}
.arrow-up{
 background:url("https://i.alipayobjects.com/i/ecmng/png/201404/2O4b2MEDzR.png") no-repeat 0 -16px;
 background-size:100%;
}
section .msg-system{
 color: #999;
 font-size: 13px;
}
section .msg-system a{
 font-size:13px;
}
section .msg-system a.isClicked{
 color:#999;
}
section.evaluateSec .answer{
 padding: 8px 12px 4px 12px;
}
.poll-loading{
 /*position: absolute;*/
 height: 24px;
 width: 24px;
 margin:0 auto;
 text-align: center;
 background: #ff9e3e;
 background:url('https://i.alipayobjects.com/i/ecmng/gif/201404/2VZeenZ86v.gif') no-repeat;
 background-size: 100%;
 z-index:86;
}
/**layer**/
 .layer{
   display: none;
   position: absolute;
   width: 275px;
   background: rgba(255, 255, 255, .95);
   border-radius: 7px;
   z-index: 120;
 }
 .layer .layer-hd{
   font-size: 16px;
   line-height: 41px;
   text-align: center;
 }
 .layer .layer-ct{
   padding:0 15px 15px 15px;
   text-align: center;
 }
 .layer .layer-ct input.tel-input{
   width: 230px;
   height: 30px;
   border: solid 1px #ccc;
   border-radius:5px;
   padding:0 4px;
 }
 .layer .layer-ft{
   border-top:solid 1px #afafaf;
   font-size: 16px;
   line-height: 41px;
   text-align: center;
 }
 .layer-ft button, .evaluateWrap button{
   border:none;
   background-color:#fff;
   opacity: .95;
   height: 44px;
   line-height: 44px;
   text-align: center;
   color:#007aff;
   width: 135px;
   font-size: 16px;
 }
.evaluateWrap button{
 width:85px;
 height:28px;
 line-height:24px;
}
 .layer .button-left, .evaluateWrap .button-cp{
   border-radius: 0 0 0 7px;
   border-right: solid 1px #afafaf;
 }
 .layer .button-right{
   border-radius: 0 0 7px 0;
 }
 .layer-mask{
   background-color: rgba(0,0,0,.6);
   position: absolute;
   top:0;
   z-index: 110;
 }
 .layer .err-msg{
   color:red;
   font-size: 12px;
   text-align: left;
   margin-top:5px;
   padding-left:5px;
 }
.evaluateWrap .title{
 border-bottom:solid 1px #afafaf;
 padding-bottom:8px;
 margin-bottom:5px;
 font-size: 16px;
 color: #000;
}
.bad-evaluate-wrap{
 width: 290px;
}
.bad-evaluate-wrap .layer-hd{
 border-bottom: solid 1px #afafaf;
}
.layer .evaluate-list li{
 line-height: 44px;
 border-bottom: solid 1px #afafaf;
}
.layer .evaluate-list li:after,.other-wrap:after{
display:block;
 clear:both;
 height:0;
 content:"\0020";
}
.layer .evaluate-list li:last-child{
 border-bottom: none;
}
.layer .evaluate-list li label{
 float: left;
 width:250px;
 overflow: hidden;
}
.layer .evaluate-list li input{
 float: right;
 margin-top: 16px;
 vertical-align: bottom;
 -webkit-appearance:checkbox;
}
.layer .evaluate-list{
 padding:0 10px;
 text-align: left;
}
.text-wrap{
 border-top: solid 1px #afafaf;
}
.text-wrap textarea{
 padding: 4px;
 width: 265px;
 height: 44px;
 border: none;
}
.layer button[disabled],
.evaluateWrap button[disabled],
.evaluateWrap button.isClicked{
 color:#afafaf;
}
.history-date{
 border-radius: 3px;
 background: #afafaf;
 color: #fff;
 width: 100px;
 margin: 0 auto;
 height: 18px;
 padding: 0 4px;
 line-height: 20px;
}
</style>



